import { useState, useEffect } from 'react';

import './TabbedPane.css';

type TabbedPaneProps = {
    groupName: string;
    activeNo: number;
    tabElements: TabElements[];
    selectTabIndex?: Function;
}

type TabElements = {
    name: string;
    disable: boolean;
    cont: JSX.Element;
}

const TabbedPane = (props: TabbedPaneProps) => {
    const [activeNo, setActiveNo] = useState(props.activeNo);

    useEffect(() => {
        setActiveNo(props.activeNo);
    }, [props.activeNo]);

    // 
    const selectTabIndex = (index: number) => {
        if (props.selectTabIndex !== undefined) {
            props.selectTabIndex(index);
        } else {
            setActiveNo(index);
        }
    };

    const tabList: JSX.Element[] = [];
    const contList: JSX.Element[] = [];
    props.tabElements.forEach((tab, index) => {
        tabList.push(
            <TabLabel
                key={index} name={tab.name} isActive={index == activeNo} isDisable={tab.disable}
                selectTab={() => selectTabIndex(index)}
            />)
            ;
        contList.push(
            <TabContents key={index} cont={tab.cont} isActive={index == activeNo} />
        );
    });
    return (
        <div id={props.groupName}>
            <div className="tab-list">
                {tabList}
            </div>
            <div className="cont-list">
                {contList}
            </div>
        </div>
    );
}

type TabLabelProps = {
    name: string;
    isActive: boolean;
    isDisable: boolean;
    selectTab: Function;
}

const TabLabel = (props: TabLabelProps) => {
    const classValues = [];
    classValues.push('tab-menu');
    classValues.push(props.isActive ? 'active' : '');
    classValues.push(props.isDisable ? 'disable' : '');
    return (
        <div className={classValues.join(' ')} onClick={() => props.selectTab()}>
            <span>{props.name}</span>
        </div>
    );
}

type TabContentsProps = {
    isActive: boolean;
    cont: JSX.Element;
}

const TabContents = (props: TabContentsProps) => {
    if(!props.isActive) {
        return (<></>);
    }
    return (
        <div className="tab-cont">
            {props.cont}
        </div>
    );
}

export default TabbedPane;