import { Dispatcher } from "contents/redux/dispatcher";
import { BackingLen, BackingLayer, BackingEditorState, BACKING_LEN_TYPE } from "contents/redux/store";
import { ChannelBean } from "./BackingTable";

type NotesMatrixProps = {
    paletteState: BackingEditorState;
    channelList: ChannelBean[];
    dispatcher: Dispatcher;
}

const NotesMatrix = (props: NotesMatrixProps) => {
    const paletteState = props.paletteState
    const channelList = props.channelList;
    const dispatcher = props.dispatcher;

    const matrixLayersJsx: JSX.Element[] = [];

    for (let l = 0; l < 2; l++) {
        const linesJsx: JSX.Element[] = [];
        const layer = paletteState.layers[l];
        const isCurLayer = paletteState.sideIndex == l;
        // 
        for (let i = channelList.length - 1; i >= 0; i--) {
            const cellList: JSX.Element[] = [];
            layer.lens.forEach((lenBean, j) => {
                let classes = '';
                let label = '';
                switch (lenBean.len) {
                    case BACKING_LEN_TYPE.LEN4: classes += 'len4'; label = '4'; break;
                    case BACKING_LEN_TYPE.LEN8: classes += 'len8'; label = '8'; break;
                    case BACKING_LEN_TYPE.LEN16: classes += 'len16'; label = '16'; break;
                    case BACKING_LEN_TYPE.LEN2: classes += 'len2'; label = '2'; break;
                    case BACKING_LEN_TYPE.LEN1: classes += 'len1'; label = '1'; break;
                }
                if (lenBean.isHalf) {
                    classes += ' half';
                }

                const divList: JSX.Element[] = [];
                const divCnt = lenBean.isDiv3 ? 3 : 1;
                for (let k = 0; k < divCnt; k++) {
                    divList.push(
                        <div
                            key={k}
                            className={
                                getActiveState(paletteState.layers, l, i, j) +
                                (lenBean.isDiv3 ? ' div3' : '')
                            }
                            onClick={() => {
                                dispatcher.backing.toggleMatrix(i, j);
                            }}
                        />
                    );
                }
                cellList.push(
                    <div
                        key={j}
                        className={classes}
                    >
                        {divList}
                    </div>
                );
            });
            linesJsx.push(
                <div key={i} className="line">
                    {cellList}
                </div>
            );
        };
        matrixLayersJsx.push(
            <div
                key={l}
                className={
                    isCurLayer ? 'cur' : 'back'
                }
            >
                {linesJsx}
            </div>
        );
    }

    return (
        <div className="matrix">
            {matrixLayersJsx}
        </div>

    );
}

/**
 * class
 * @param layers 
 * @param curLayerIndex 
 * @param lineIndex 
 * @param lenIndex 
 * @returns class
 */
const getActiveState = (layers: BackingLayer[], curLayerIndex: number, lineIndex: number, lenIndex: number): string => {
    const cur = layers[curLayerIndex == 0 ? 0 : 1];
    const back = layers[curLayerIndex == 1 ? 0 : 1];
    let className = '';

    if (cur.table[lineIndex][lenIndex][0]) {
        className = 'active';
        const curCellRange = getCellRange(cur.lens, lenIndex);
        back.lens.forEach((backLenBean, j) => {
            const judge1 = isOverlapJudge(curCellRange, getCellRange(back.lens, j));
            const judge2 = isOverlapJudge(getCellRange(back.lens, j), curCellRange);
            if (back.table[lineIndex][j][0] && (judge1 || judge2)) {
                className = 'overlap';
            }
        });
    }
    return className;
}

/**
 * 2
 * @param range1 1
 * @param range2 2
 * @returns 
 */
const isOverlapJudge = (range1: CellRange, range2: CellRange): boolean => {
    return (range1.head + range1.width > range2.head && range1.head + range1.width <= range2.head + range2.width)
        || (range1.head < range2.head + range2.width && range1.head >= range2.head);
}

/**
 * 
 * @param lens 
 * @param lenIndex 
 * @returns 
 */
const getCellRange = (lens: BackingLen[], lenIndex: number): CellRange => {
    let head = 0;
    for (let i = 0; i < lenIndex; i++) {
        const lenBean = lens[i];
        head += getLenSize(lenBean);
    }
    const width = getLenSize(lens[lenIndex]);
    return { head, width };
}

/**
 * 
 */
type CellRange = {
    head: number;
    width: number;
}

/**
 * 
 * @param lenBean 
 * @returns 
 */
const getLenSize = (lenBean: BackingLen): number => {
    let size = 0;
    switch (lenBean.len) {
        case BACKING_LEN_TYPE.LEN1: size = 16; break;
        case BACKING_LEN_TYPE.LEN2: size = 8; break;
        case BACKING_LEN_TYPE.LEN4: size = 4; break;
        case BACKING_LEN_TYPE.LEN8: size = 2; break;
        case BACKING_LEN_TYPE.LEN16: size = 1; break;
    }
    if (lenBean.isHalf) {
        size *= 1.5;
    }
    return size;
}

export default NotesMatrix;