import { SymbolParams } from "contents/common/define";
import { BackingEditorState } from "contents/redux/store";


type StructMatrixProps = {
    paletteState: BackingEditorState;
    structs: string[];
    symbol: SymbolParams;
    degreeIndex: number;
    testPlaySound: Function;
    setUseChannels: Function
}

export const StructMatrix = (props: StructMatrixProps) => {

    const structTable: JSX.Element[] = [];
    const ChordStructMax = 7;
    const OctaveMax = 8;
    for (let i = 0; i < ChordStructMax; i++) {
        // for (let i = ChordStructMax - 1; i >= 0; i--) {
        const isUse = i < props.symbol.intervals.length;
        const cols: JSX.Element[] = [];
        if (isUse) {
            const soundName = props.structs[i];
            const interval = props.symbol.intervals[i];
            const relation = props.symbol.struct[i];
            // 
            cols.push(
                <div key={0} className="label">
                    <span>{relation}[{interval}]: {soundName}</span>
                </div>
            );
            // 
            for (let j = 0; j < OctaveMax; j++) {
                // for (let j = OctaveMax - 1; j >= 0; j--) {
                const soundIndex = props.degreeIndex + props.symbol.intervals[i] + j * 12;
                const octave = Math.floor(soundIndex / 12);
                const soundFullName = props.structs[i] + octave;
                const isNone = soundIndex < 9 || soundIndex >= 97;
                let isSelected = false;
                props.paletteState.useChannels.forEach((channel) => {
                    if (channel.octave == j && channel.structIndex == i) {
                        isSelected = true;
                    }
                });
                // 
                const isLimit = props.paletteState.useChannels.length >= 12 && !isSelected;
                cols.push(
                    <div
                        key={j + 1}
                        className={
                            'col' +
                            (isNone || isLimit ? ' disable' : '') +
                            (isSelected ? ' active' : '')
                        }
                        onClick={() => {
                            const list = props.paletteState.useChannels;
                            if (!isSelected) {
                                list.push({ octave: j, structIndex: i });
                            } else {
                                let pos = -1;
                                list.forEach((channel, index) => {
                                    if (channel.octave == j && channel.structIndex == i) {
                                        pos = index;
                                    }
                                });
                                list.splice(pos, 1);
                            }
                            list.sort((a, b) => {
                                const aVal = a.octave * 8 + a.structIndex;
                                const bVal = b.octave * 8 + b.structIndex;
                                return aVal - bVal;;
                            });
                            props.paletteState.useChannels = list;
                            props.setUseChannels(props.paletteState.useChannels);
                        }}
                        onContextMenu={() => {
                            props.testPlaySound(soundFullName);
                        }}
                    >
                        <span>{!isNone ? soundFullName : '-'}</span>
                    </div>
                );
            }
        } else {
            // 
            cols.push(
                <div key={0} className="label"></div>
            );
            for (let j = 0; j < 7; j++) {
                cols.push(
                    <div key={j + 1} className="col"></div>
                );
            }
        }
        structTable.push(
            <div
                key={i}
                className={
                    'record' +
                    (!isUse ? ' disable' : '')
                }
            >
                {cols}
            </div>
        );
    }

    return (
        <div className="struct-table">
            {structTable}
        </div>
    );
}

export default StructMatrix;