import { Dispatcher } from "contents/redux/dispatcher";
import { BackingEditorState, BACKING_LEN_TYPE, ChannelParam } from "contents/redux/store";
import BackingOperation from "./BackingOperation";
import NotesMatrix from "./NotesMatrix";
import SplitMeasure from "./SplitMeasure";


export type ChannelBean = {
    soundIndex: number;
    soundFullName: string;
}

type BackingTableProps = {
    paletteState: BackingEditorState;
    dispatcher: Dispatcher;
    channelList: ChannelBean[];
    testPlaySound: Function;
}

const BackingTable = (props: BackingTableProps) => {

    const paletteState = props.paletteState;
    const sideIndex = paletteState.sideIndex;
    const curSideScript = paletteState.layers[sideIndex];
    const dispatcher = props.dispatcher;
    const channelList = props.channelList;
    const testPlaySound = props.testPlaySound;


    const channelJsxList: JSX.Element[] = [];
    channelList.forEach((channel, i) => {
        channelJsxList.push(
            <div
                key={i}
                className="channel"
                onClick={() => testPlaySound(channel.soundFullName)}
            >
                <span>{channel.soundFullName}</span>
            </div>
        );
    });

    // 
    const lens: JSX.Element[] = [];
    const volumeList: JSX.Element[] = [];
    curSideScript.lens.forEach((lenBean, i) => {
        const isSelected = paletteState.selectedLenIndex == i;
        let classes = isSelected ? 'active ' : '';
        let label = '';
        switch (lenBean.len) {
            case BACKING_LEN_TYPE.LEN4: classes += 'len4'; label = '4'; break;
            case BACKING_LEN_TYPE.LEN8: classes += 'len8'; label = '8'; break;
            case BACKING_LEN_TYPE.LEN16: classes += 'len16'; label = '16'; break;
            case BACKING_LEN_TYPE.LEN2: classes += 'len2'; label = '2'; break;
            case BACKING_LEN_TYPE.LEN1: classes += 'len1'; label = '1'; break;
        }
        if (lenBean.isHalf) {
            label += ' .';
            classes += ' half';
        }
        if (lenBean.isDiv3) {
            label += ' /';
        }
        lens.push(
            <div
                key={i}
                className={classes}
                onClick={() => {
                    dispatcher.initBackingPalette({
                        ...paletteState,
                        selectedLenIndex: i
                    });
                }}>
                <span>{label}</span>
            </div>
        );
        const volume = curSideScript.vels[i];
        volumeList.push(
            <div
                key={i}
                className={classes}
            >
                <div style={{ height: (volume * 10) + '%' }}></div>
            </div>
        );
    });

    return (
        <div className="backing-table">
            <div className="switch">
                <BackingOperation
                    dispatcher={dispatcher}
                    paletteState={paletteState}
                />
            </div>
            <div className="fixed">
                <div className="layer">
                    <button
                        className={
                            paletteState.sideIndex == 1 ? 'active' : ''
                        }
                        onClick={() => {
                            dispatcher.backing.setLayerIndex(1);
                        }}
                    >
                        <span>RIGHT</span>
                    </button>
                    <button
                        className={
                            paletteState.sideIndex == 0 ? 'active' : ''
                        }
                        onClick={() => {
                            dispatcher.backing.setLayerIndex(0);
                        }}
                    >
                        <span>LEFT</span>
                    </button>
                </div>
                <div className="matrix">
                    {channelJsxList}
                </div>
                {/* <div className="volume"></div> */}
            </div>
            <div className="main">
                <div className="split">
                    {lens}
                </div>
                <SplitMeasure
                    layer={curSideScript}
                />
                <NotesMatrix
                    paletteState={paletteState}
                    channelList={channelList}
                    dispatcher={dispatcher}
                />
                <div className="volume">
                    {volumeList}
                </div>
            </div>
        </div>
    );
}

export default BackingTable;