import { GlobalContext } from "contents/MainFrame";
import { ActionType } from "contents/redux/reducer";
import { DetailChord, FloatWndState, TypeNoteElement } from "contents/redux/store";
import { useContext, useEffect, useRef } from "react";

import './NoteContextMenu.css';

const NoteContextMenu = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    /**  */
    const close = () => dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });

    /**  */
    const ref = useRef({} as HTMLDivElement);
    useEffect(() => {
        // dispatch(ActionType.GET_REFFER, { reffers: {...state.reffers, contextMneu: ref.current }});
        dispatch(ActionType.GET_REFFER_CONTEXT, { reffer: ref.current });
    }, []);

    const menuPropsList: MenuRecordProps[] = [];
    if (state.noteState.floatwndState == FloatWndState.CONTEXT_MENU) {
        const curElement = state.noteData.dataList[state.noteState.focusIndex];
        menuPropsList.push({
            menuType: 'order', label: 'INSERT BLANK', keyName: 'A', disable: false,
            executer: () => {
                dispatch(ActionType.ADD_CHORD, { index: state.noteState.focusIndex });
                close();
            }
        });
        menuPropsList.push({
            menuType: 'call', label: 'INSERT CHORD', keyName: 'W', disable: false,
            executer: () => {
                dispatch(ActionType.ADD_CHORD, { index: state.noteState.focusIndex });
                dispatch(ActionType.SELECT_NOTE_ELEMENT, { index: state.noteState.focusIndex + 1 });
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHORD_PALETTE });
                const detail = curElement.detail as DetailChord;
                dispatch(ActionType.CHORD_PALETTE_SETTER, {
                    params: {
                        ...state.noteState.chordPaletteState,
                        isAddition: true,
                        rootIndex: detail.degreeIndex,
                        isFlat: detail.isFlat,
                        symbolKey: detail.symbolKey,
                        sustain: detail.sustain,
                    }
                });
            }
        });
        menuPropsList.push({
            menuType: 'order', label: 'INSERT SECTION', keyName: 'S', disable: false,
            executer: () => {
                dispatch(ActionType.ADD_SECTION, { index: state.noteState.focusIndex });
                close();
            }
        });
        menuPropsList.push({
            menuType: 'order', label: 'INSERT MODULATION', keyName: 'M', disable: false,
            executer: () => { }
        });
        switch (curElement.type) {
            case TypeNoteElement.Chord: {
                const detail = curElement.detail as DetailChord;
                menuPropsList.push({
                    menuType: 'order', label: 'BLANK CHORD', keyName: 'D', disable: detail.degreeIndex == -1,
                    executer: () => {
                        dispatch(ActionType.SET_DEGREE_INDEX, { pos: state.noteState.focusIndex, degreeIndex: -1 });
                        close();
                    }
                });
                menuPropsList.push({
                    menuType: 'call', label: 'EDIT CHORD', keyName: 'C', disable: false,
                    executer: () => {
                        // state.reffers.chordPalette.style.left = 50 + 'px';
                        // state.reffers.chordPalette.style.top = 50 + 'px';
                        dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHORD_PALETTE });
                        dispatch(ActionType.CHORD_PALETTE_SETTER, {
                            params: {
                                ...state.noteState.chordPaletteState,
                                rootIndex: detail.degreeIndex,
                                isFlat: detail.isFlat,
                                symbolKey: detail.symbolKey,
                                sustain: detail.sustain,
                            }
                        });
                    }
                });
                menuPropsList.push({
                    menuType: 'call', label: 'EDIT BACKING', keyName: 'B', disable: false,
                    executer: () => {
                        dispatcher.score.callBackingEditor();
                     }
                });
            }
                break;
            case TypeNoteElement.Section: {
                menuPropsList.push({
                    menuType: 'call', label: 'EDIT SECTION', keyName: 'C', disable: false,
                    executer: () => { }
                });
            }
                break;
        }
        menuPropsList.push({
            menuType: 'order', label: 'REMOVE', keyName: 'DEL', disable: true,
            executer: () => { }
        });
    }
    const menuList: JSX.Element[] = [];
    menuPropsList.forEach((data, i) => {
        menuList.push(<MenuRecord
            key={i}
            menuType={data.menuType}
            label={data.label}
            keyName={data.keyName}
            disable={data.disable}
            executer={data.executer}
        />);
    });

    return (
        <div
            ref={ref}
            id="note-contextmenu"
            className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.CONTEXT_MENU ? ' active' : '')}
            onClick={() => {
                // dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            }}
        >
            {menuList}
        </div>
    );
}

type MenuRecordProps = {
    menuType: string;
    label: string;
    keyName: string;
    disable: boolean;
    executer: Function;
}
const MenuRecord = (props: MenuRecordProps) => {

    return (
        <div className={props.disable ? 'disable' : ''}
            onClick={() => {
                props.executer();
            }}
        >
            <span className={props.menuType}>{props.label}</span>
            <span className="key">{`<${props.keyName} key>`}</span>
        </div>
    );
}

export default NoteContextMenu;