import { DEGREE12_FLAT_LIST, DEGREE12_SHARP_LIST, KEY12_FLAT_LIST, KEY12_SHARP_LIST, SYMBOL_PARAMS_LIST } from "contents/common/define";
import TabbedPane from "contents/common/TabbedPane";
import { GlobalContext } from "contents/MainFrame";
import { ActionType } from "contents/redux/reducer";
import { DetailChord, FloatWndState, ChordPaletteLine, TypeNoteElement, EDIT_MODE } from "contents/redux/store";
import { useContext, useEffect, useRef, useState } from "react";
// import styled from 'styled-components';
// npm i --save-dev @types/styled-components
import './ChordEditor.css';

const ChordSelector = () => {

    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    // const [degreeIndex, setDegreeIndex] = useState(0);
    // const [isFlat, setFlat] = useState(false);
    // const [symbolKey, setSymbolKey] = useState(SYMBOL_PARAMS_LIST[0].key);
    // const [sustain, setSustain] = useState(4);

    /**  */
    const ref = useRef({} as HTMLDivElement);
    useEffect(() => {
        // dispatch(ActionType.GET_REFFER, { reffers: {...state.reffers, chordPalette: ref.current }});
        dispatch(ActionType.GET_REFFER_CHORD_PALETTE, { reffer: ref.current });
    }, []);

    const chordList: JSX.Element[] = [];
    const symbolList: JSX.Element[] = [];
    const beatList: JSX.Element[] = [];
    let operation: JSX.Element = <></>;
    const isDirect = state.noteData.editMode == EDIT_MODE.DIRECT;

    const editorState = state.noteState.chordPaletteState;
    if (state.noteState.floatwndState == FloatWndState.CHORD_PALETTE) {
        const focusIndex = state.noteState.focusIndex;
        const chordDetail = state.noteData.dataList[focusIndex].detail as DetailChord;

        DEGREE12_SHARP_LIST.forEach((degS, i) => {
            const baseKey = 0;
            const keyIndex = (baseKey + i) % 12;
            const degF = DEGREE12_FLAT_LIST[i];
            const chdS = KEY12_SHARP_LIST[keyIndex];
            const chdF = KEY12_FLAT_LIST[keyIndex];
            const isHalf = [1, 3, 6, 8, 10].includes(i);
            const isSelected = editorState.rootIndex == i;
            const buildLabelSrc = (isFlat: boolean): JSX.Element => {
                return (
                    state.noteData.editMode == EDIT_MODE.SCALE ?
                        <>
                            <span className="degree">{isFlat ? degF : degS}</span>
                            <span>&nbsp;(</span>
                            <span className="chord">{isFlat ? chdF : chdS}</span>
                            <span>)</span>
                        </>
                        :
                        <>
                            <span className="degree">{isFlat ? chdF : chdS}</span>
                        </>
                );
            };
            chordList.push(
                <div key={i}>{
                    !isHalf ?
                        <div
                            className={isSelected ? 'active' : ''}
                            onClick={() => {
                                dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                    params: {
                                        ...editorState,
                                        selectedLine: ChordPaletteLine.Root, rootIndex: i, isFlat: false
                                    }
                                });
                            }}
                        >
                            {buildLabelSrc(false)}
                        </div>
                        :
                        <>
                            <div
                                className={"half" + (isSelected && !editorState.isFlat ? ' active' : '')}
                                onClick={() => {
                                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                        params: {
                                            ...editorState,
                                            selectedLine: ChordPaletteLine.Root, rootIndex: i, isFlat: false
                                        }
                                    });
                                }}
                            >
                                {buildLabelSrc(false)}
                            </div>
                            <div
                                className={"half" + (isSelected && editorState.isFlat ? ' active' : '')}
                                onClick={() => {
                                    dispatch(ActionType.CHORD_PALETTE_SETTER, {
                                        params: {
                                            ...editorState,
                                            selectedLine: ChordPaletteLine.Root, rootIndex: i, isFlat: true
                                        }
                                    });
                                }}
                            >
                                {buildLabelSrc(true)}
                            </div>
                        </>
                }</div>
            );
        });

        SYMBOL_PARAMS_LIST.forEach((symbol, i) => {
            // const isSelected = chordDetail.degreeIndex == i;
            symbolList.push(
                <div
                    key={i}
                    className={symbol.key == editorState.symbolKey ? 'active' : ''}
                    onClick={() => {
                        dispatch(ActionType.CHORD_PALETTE_SETTER, {
                            params: {
                                ...editorState,
                                selectedLine: ChordPaletteLine.Symbol, symbolKey: symbol.key
                            }
                        });
                    }}
                >
                    <span>[{symbol.name}]</span>
                </div>

            );
        });

        for (let i = 0; i < 8; i++) {
            beatList.push(
                <div
                    key={i}
                    className={editorState.sustain >= i + 1 ? 'active' : ''}
                    onClick={() => {
                        dispatch(ActionType.CHORD_PALETTE_SETTER, {
                            params: {
                                ...editorState, selectedLine: ChordPaletteLine.Sustain, sustain: i + 1
                            }
                        });
                    }}
                />
            );
        }

        operation = (<>
            <div
                onClick={() => {
                    dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                }}>
                <span>Cancel</span>
            </div>
            <div
                onClick={() => {
                    dispatcher.chord.setCurChordParam();
                }}>
                <span>Enter</span>
            </div>
        </>);
    }
    return (
        <div
            ref={ref}
            id="total-palette"
            className={'floatwnd' + (state.noteState.floatwndState == FloatWndState.CHORD_PALETTE ? ' active' : '')}
            onClick={() => {
            }}
        >
            <TabbedPane groupName="selector-tabs"
                tabElements={[
                    {
                        name: 'Scale', disable: isDirect, cont: (
                            <>
                            </>
                        )
                    },
                    {
                        name: 'Separate', disable: false, cont: (
                            <>
                                <div className={'chord-selector' + (editorState.selectedLine == ChordPaletteLine.Root ? ' active' : '')}>
                                    {chordList}
                                </div>
                                <div className={'symbol-selector' + (editorState.selectedLine == ChordPaletteLine.Symbol ? ' active' : '')}>
                                    {symbolList}
                                </div>
                            </>
                        )
                    },
                    {
                        name: 'Fragment', disable: false, cont: (
                            <>
                            </>
                        )
                    }
                ]}
                activeNo={1}
            />
            <div className={'beat-selector' + (editorState.selectedLine == ChordPaletteLine.Sustain ? ' active' : '')}>
                {beatList}
            </div>
            <div className="operation">
                {operation}
            </div>
        </div>
    );
}


export default ChordSelector;