import { DIATONIC_MAJOR_SCALE_LIST, KEY12_SHARP_LIST, SYMBOL_PARAMS_LIST } from "contents/common/define";
import { getSymbolParams } from "contents/common/utils";
import { Dispatcher } from "contents/redux/dispatcher";
import { ActionType } from "contents/redux/reducer";
import { BackingEditorState as BackingEditorState, BACKING_LEN_TYPE, BACKING_SIDE, DetailChord, FloatWndState, NoteElement, Store, TypeNoteElement } from "contents/redux/store";
import { KeyboardEvent } from 'react';


/**
 * 
 * @param e 
 */
export const KeyboardShortCutAction = (e: KeyboardEvent, state: Store, dispatch: Function, dispatcher: Dispatcher) => {
    // alert(e.key);

    const focusIndex = state.noteState.focusIndex;

    const selectedData = state.noteData.dataList[focusIndex];
    switch (selectedData.type) {
        case TypeNoteElement.Chord: {
            const detail = selectedData.detail as DetailChord;

            switch (state.noteState.floatwndState) {
                case FloatWndState.NONE: {
                    switch (e.key) {
                        case 'c': {
                            dispatcher.score.callChordSelector();
                        }
                            break;
                        case 'b': {
                            dispatcher.score.callBackingEditor();
                        }
                            break;
                        case '1':
                        case '2':
                        case '3':
                        case '4':
                        case '5':
                        case '6':
                        case '7':
                            const diatonicChord = DIATONIC_MAJOR_SCALE_LIST[Number(e.key) - 1];
                            dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: diatonicChord.degreeIndex });
                            dispatch(ActionType.SET_SYMBOL_KEY, { pos: focusIndex, symbolKey: diatonicChord.symbolKey });
                            break;
                    }
                }
                    break;
                case FloatWndState.CHORD_PALETTE: {
                    switch (e.key) {
                        case 'Escape':
                        case 'c':
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                            break;
                        case 'ArrowRight':
                            dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: detail.degreeIndex + 1 });
                            break;
                        case 'ArrowLeft':
                            dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: detail.degreeIndex - 1 });
                            break;
                        case '1':
                            dispatch(ActionType.SET_DEGREE_INDEX, { pos: focusIndex, degreeIndex: detail.degreeIndex - 1 });
                            break;
                        case 'Enter': {
                            dispatcher.chord.setCurChordParam();
                        }
                            break;
                    }
                }
                    break;
                case FloatWndState.BACKING_PALETTE: {
                    switch (e.key) {
                        case 'Escape':
                        case 'b':
                            dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
                            break;
                        case 'ArrowLeft': {
                            if (!dispatcher.backing.isFirstCurLen()) {
                                const cur = dispatcher.backing.getCurLenIndex();
                                dispatcher.backing.updateLenIndex(cur - 1);
                            }
                        }
                            break;
                        case 'ArrowRight': {
                            if (!dispatcher.backing.isLastCurLen()) {
                                const cur = dispatcher.backing.getCurLenIndex();
                                dispatcher.backing.updateLenIndex(cur + 1);
                            }
                        }
                            break;
                        case 'ArrowUp': {
                            const curVolume = dispatcher.backing.getCurVelocity();
                            if (curVolume < 10) {
                                dispatcher.backing.updateCurVolume(curVolume + 1);
                            }
                        }
                            break;
                        case 'ArrowDown': {
                            const curVolume = dispatcher.backing.getCurVelocity();
                            if (curVolume > 1) {
                                dispatcher.backing.updateCurVolume(curVolume - 1);
                            }
                        }
                            break;
                        case 'r': {
                            dispatcher.backing.changeLayerIndex();
                        }
                            break;
                        case '1': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN16); break;
                        case '2': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN8); break;
                        case '3': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN4); break;
                        case '4': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN2); break;
                        case '5': dispatcher.backing.setCurLen(BACKING_LEN_TYPE.LEN1); break;
                        case '.': {
                            if (dispatcher.backing.isAcceptHalf()) {
                                const curLenBean = dispatcher.backing.getCurLenBean();
                                curLenBean.isHalf = !curLenBean.isHalf;
                                curLenBean.isDiv3 = false;
                                dispatcher.backing.setCurrentLenBean(curLenBean);
                            }
                        }
                            break;
                        case '/': {
                            if (dispatcher.backing.isAcceptDiv3()) {
                                const curLenBean = dispatcher.backing.getCurLenBean();
                                curLenBean.isDiv3 = !curLenBean.isDiv3;
                                curLenBean.isHalf = false;
                                dispatcher.backing.setCurrentLenBean(curLenBean);
                            }
                        }
                            break;
                        case 'a': {
                            dispatcher.backing.addLen();
                        }
                            break;
                        case 'Delete': {
                            if (dispatcher.backing.getSideScript().lens.length > 1) {
                                dispatcher.backing.delLen();
                            }
                        }
                            break;

                        case ' ': {
                            dispatcher.backing.play();
                        }
                            break;
                        // 
                        case 'Enter': {
                            dispatcher.backing.setCurBackingScript();
                        }
                            break;
                    }
                }
                    break;
            }
        }
            break;
    }

    if (state.noteState.floatwndState == FloatWndState.NONE &&
        [TypeNoteElement.Chord, TypeNoteElement.Section].includes(selectedData.type)) {
        switch (e.key) {
            case 'ArrowLeft': {
                dispatcher.score.focusPreviousElement();
            }
                break;
            case 'ArrowRight': {
                dispatcher.score.focusNextElement();
            }
                break;
            case 'ArrowUp': {
                dispatcher.score.focusPreviousSection();
            }
                break;
            case 'ArrowDown': {
                dispatcher.score.focusNextSection();
            }
                break;
            case 'Delete': {
                dispatcher.score.deleteCurElement();
                // dispatcher.score.addChordElement();
            }
                break;
            case 'i': {
                dispatcher.score.focusInitElement();
            }
                break;
            case 'a':
                // dispatch(ActionType.ADD_CHORD, { index: focusIndex });
                dispatcher.score.addChordElement();
                break;
            case 's':
                dispatch(ActionType.ADD_SECTION, { index: focusIndex });
                break;
            case 'w': {
                const detail = selectedData.detail as DetailChord;
                dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHORD_PALETTE });
                dispatch(ActionType.CHORD_PALETTE_SETTER, {
                    params: {
                        ...state.noteState.chordPaletteState,
                        rootIndex: detail.degreeIndex,
                        isFlat: detail.isFlat,
                        symbolKey: detail.symbolKey,
                        sustain: detail.sustain,
                    }
                });
            }
        }
    }
};