
import { GlobalContext } from 'contents/MainFrame';
import { DetailSection, TypeNoteElement } from 'contents/redux/store';
import { useContext, useEffect, useState } from 'react';

type OutlineSectionProps = {
    label: String;
    index: number;
}
const OutlineSection = (props: OutlineSectionProps) => {
    const { state, dispatch } = useContext(GlobalContext);

    const isCurrent = (): boolean => {
        const dataList = state.noteData.dataList;
        const focusIndex = state.noteState.focusIndex;
        let secNo = -1;
        let curSecNo = -1;
        dataList.forEach((el, i) => {
            if (el.type == TypeNoteElement.Section) secNo++;
            if (focusIndex == i) curSecNo = secNo;
        });
        return curSecNo == props.index;
    }
    return (
        <div className={isCurrent() ? 'active' : ''}>
            <span>{props.label}</span>
        </div>
    );
}

export default OutlineSection;
