import { DEGREE12_FLAT_LIST, DEGREE12_SHARP_LIST, DIATONIC_MAJOR_SCALE_DEGREE_NAMES, KEY12_FLAT_LIST, KEY12_SHARP_LIST, SYMBOL_PARAMS_LIST } from "contents/common/define";
import { GlobalContext } from "contents/MainFrame";
import { DetailChord, DetailFixed, DetailInit, DetailModulation, DetailSection, EDIT_MODE, NoteElement, TypeNoteElement } from "contents/redux/store";
import { useContext } from "react";
import { ProgressInfo } from "../ScoreElement";

type ScoreElementDetailProps = {
    noteElement: NoteElement;
    progress: ProgressInfo;
}

const ScoreElementDetail = (props: ScoreElementDetailProps) => {
    
    const { state, dispatch, dispatcher } = useContext(GlobalContext);

    switch (props.noteElement.type) {
        case TypeNoteElement.Fixed: {
            const detail = props.noteElement.detail as DetailFixed;
            return (
                <span>{detail.label}</span>
            );
        }
        case TypeNoteElement.Init: {
            const detail = props.noteElement.detail as DetailInit;
            const paramLabels: JSX.Element[] = [];
            paramLabels.push(<div><span>tempo: </span><span>{detail.tempo}</span></div>);
            paramLabels.push(<div><span>rythm: </span><span>{detail.rythm}</span></div>);
            if (state.noteData.editMode == EDIT_MODE.SCALE) {
                paramLabels.push(<div><span>key: </span><span>{detail.key}</span></div>);
                paramLabels.push(<div><span>scale: </span><span>{detail.scale}</span></div>);
            }
            return (
                <div>
                    {paramLabels}
                </div>
            );
        }
        case TypeNoteElement.Chord: {
            const detail = props.noteElement.detail as DetailChord;

            /** */
            const getDegreeRoot = (index: number): string => {
                let name = 'blank';
                if (index != -1) {
                    const list = detail.isFlat ? DEGREE12_FLAT_LIST : DEGREE12_SHARP_LIST;
                    name = list[index];
                }
                return name;
            }
            /** */
            const getChordName = (index: number): string => {
                let name = '-';
                if (index != -1) {
                    const list = detail.isFlat ? KEY12_FLAT_LIST : KEY12_SHARP_LIST;
                    name = list[index];
                }
                return name;
            }
            /** */
            const getSymbol = (index: number, symbolKey: string): string => {
                let name = '';
                if (index != -1) {
                    SYMBOL_PARAMS_LIST.forEach((symbol, i) => {
                        if (symbol.key == symbolKey) {
                            name = symbol.name;
                        }
                    });
                }
                return name;
            }

            const degreeRoot = getDegreeRoot(detail.degreeIndex);
            const chordName = getChordName(detail.degreeIndex);
            const symbolName = getSymbol(detail.degreeIndex, detail.symbolKey);
            const isDiatonic = DIATONIC_MAJOR_SCALE_DEGREE_NAMES.includes(degreeRoot + symbolName);
            const sustainJsx = [];
            for (let i = 0; i < detail.sustain; i++) {
                sustainJsx.push(<span key={i}></span>);
            }
            const barCount = props.progress.barCount;
            const remaindBeat = props.progress.remaindBeat;
            const headMSec = Math.round(props.progress.headMSec / 1000 * 10) / 10;
            // alert(detail.degreeIndex);
            const notesCount = dispatcher.backing.getNotesCount(detail.backing);
            return (
                <>
                    <div className={
                        'degree'
                        + (detail.degreeIndex == -1 ? ' blank' : '')
                        + (isDiatonic ? ' diatonic' : '')
                        + (state.noteData.editMode == EDIT_MODE.DIRECT ? ' visible' : '')}>
                        <span>{degreeRoot}{symbolName}</span>
                    </div>
                    <div className={state.noteData.editMode == EDIT_MODE.DIRECT ? 'degree' : 'chord'}>
                        <span>{chordName}{symbolName}</span>
                    </div>
                    <div className="sustain">
                        {sustainJsx}
                    </div>
                    <div className="progress-sustain">
                        <span className="bar">{barCount != -1 ? barCount : ''}</span>
                        <span className="beat">{remaindBeat != -1 ? remaindBeat : ''}</span>
                    </div>
                    <div className="progress-time">
                        <span>{headMSec}s</span>
                    </div>
                    <div
                        className={
                            'backing-script' +
                            (notesCount == 0 ? ' none' : '')
                        }
                    >
                        <span>notes: {notesCount}</span>
                    </div>
                </>
            );
        }
        case TypeNoteElement.Section: {
            const detail = props.noteElement.detail as DetailSection;
            return (
                <>
                    <div className="name"><span>{detail.label}</span></div>
                    <div className="border"></div>
                </>
            );
        }
        case TypeNoteElement.Modulation: {
            const detail = props.noteElement.detail as DetailModulation;
            return (
                <></>
            );
        }
    }
}

export default ScoreElementDetail;