import { KEY12_SHARP_LIST } from "contents/common/define";
import { getSymbolParams } from "contents/common/utils";
import { ActionType } from "./reducer";
import { Action, BackingLen, BackingEditorState, BackingLayer, BACKING_LEN_TYPE, BACKING_SIDE, ChannelParam, DetailInit, NoteData, Store, TypeNoteElement, NoteState, NoteElement, FloatWndState, DetailChord, BackingScript } from "./store";

/**
 * 
 *
 * @export
 * @class Dispatcher
 */
export class Dispatcher {

    private dispatchBase: React.Dispatch<Action>;
    private dispatch: Function;
    private state: Store;

    /**
     *Creates an instance of Dispatcher.
     * @param {Store} state
     * @param {React.Dispatch<Action>} dispatchBase
     * @memberof Dispatcher
     */
    constructor(state: Store, dispatchBase: React.Dispatch<Action>) {
        this.dispatchBase = dispatchBase;
        this.state = state;
        this.dispatch = (type: ActionType, payload: any) => dispatchBase({ type, payload });
    }

    /**
     * 
     * @param noteData 
     */
    public setNoteData = (data: NoteData) => {
        this.dispatchBase({ type: ActionType.SET_NOTE_DATA, payload: { data } });
    }
    public initBackingPalette = (backingPaletteState: BackingEditorState) => {
        this.dispatchBase({ type: ActionType.INIT_BACKING_PALETTE, payload: { backingPaletteState } });
    }

    public setUseChannels = (useChannels: ChannelParam[]) => {
        this.dispatchBase({ type: ActionType.SET_USE_CHANNELS, payload: { useChannels } });
    }

    public score = {
        /**
         * 
         */
        getInitElement: (): DetailInit => {
            return this.state.noteData.dataList[0].detail as DetailInit;
        },
        addChordElement: () => {
            const curIndex = this.state.noteState.focusIndex;
            const dataList = this.state.noteData.dataList.slice();
            dataList.splice(curIndex + 1, 0, {
                type: TypeNoteElement.Chord,
                detail: {
                    degreeIndex: -1,
                    isFlat: false,
                    symbolKey: null,
                    sustain: 4,
                    backing: {
                        useChannels: [],
                        layers: [
                            {
                                lens: [{
                                    len: BACKING_LEN_TYPE.LEN1,
                                    isDiv3: false,
                                    isHalf: false
                                }],
                                side: BACKING_SIDE.LEFT,
                                table: [],
                                vels: [5]
                            },
                            {
                                lens: [{
                                    len: BACKING_LEN_TYPE.LEN1,
                                    isDiv3: false,
                                    isHalf: false
                                }],
                                side: BACKING_SIDE.RIGHT,
                                table: [],
                                vels: [5]
                            }
                        ]
                    }
                }
            });
            this.dispatch(ActionType.SET_DATA_LIST, { dataList });
        },
        deleteCurElement: () => {
            const curIndex = this.state.noteState.focusIndex;
            const curElement = this.state.noteData.dataList[curIndex];
            if (curElement.type == TypeNoteElement.Chord) {
                const dataList: NoteElement[] = [];//this.state.noteData.dataList.slice();
                this.state.noteData.dataList.forEach((data, i) => {
                    if (curIndex != i) {
                        dataList.push({ ...data });
                    }
                });
                dataList.splice(curIndex, 1);
                this.state.noteState.focusIndex--;
                this.dispatch(ActionType.SET_DATA_LIST, { dataList });
                this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
            }
        },

        focusPreviousElement: () => {
            const curIndex = this.state.noteState.focusIndex;
            const prev = this.state.noteData.dataList[curIndex - 1];
            if (prev.type != TypeNoteElement.Fixed) {
                this.state.noteState.focusIndex--;
                this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
            }
        },

        focusNextElement: () => {
            const curIndex = this.state.noteState.focusIndex;
            const next = this.state.noteData.dataList[curIndex + 1];
            if (next.type != TypeNoteElement.Fixed) {
                this.state.noteState.focusIndex++;
                this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
            }
        },

        focusPreviousSection: () => {
            const dataList = this.state.noteData.dataList;
            let curIndex = this.state.noteState.focusIndex;
            do {
                curIndex--;
                if (dataList[curIndex].type == TypeNoteElement.Section) {
                    this.state.noteState.focusIndex = curIndex;
                    this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
                    break;
                }
            } while (curIndex > 0);
        },

        focusNextSection: () => {
            const dataList = this.state.noteData.dataList;
            let curIndex = this.state.noteState.focusIndex;
            do {
                curIndex++;
                if (dataList[curIndex].type == TypeNoteElement.Section || curIndex == dataList.length - 2) {
                    this.state.noteState.focusIndex = curIndex;
                    this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
                    break;
                }
            } while (curIndex < dataList.length - 1);
        },

        focusInitElement: () => {
            this.state.noteState.focusIndex = 0;
            this.dispatch(ActionType.SET_NOTE_STATE, { noteState: this.state.noteState });
        },

        callChordSelector: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.CHORD_PALETTE });
            this.dispatch(ActionType.CHORD_PALETTE_SETTER, {
                params: {
                    ...this.state.noteState.chordPaletteState,
                    rootIndex: detail.degreeIndex,
                    isFlat: detail.isFlat,
                    symbolKey: detail.symbolKey,
                    sustain: detail.sustain,
                }
            });
        },

        callBackingEditor: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.BACKING_PALETTE });
            const backing = detail.backing;
            const backingState: BackingEditorState = {
                useChannels: backing.useChannels,
                sideIndex: 0,
                selectedLenIndex: 0,
                selectedChannelIndex: -1,
                layers: backing.layers
            };
            this.initBackingPalette(backingState);
        }
    }


    /**
     * 
     *
     * @memberof Dispatcher
     */
    public chord = {
        setCurChordParam: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const chordDetail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            const editorState = this.state.noteState.chordPaletteState;
            this.dispatch(ActionType.DETAIL_PARAMS_SETTER, {
                pos: focusIndex,
                params: {
                    ...chordDetail,
                    degreeIndex: editorState.rootIndex,
                    isFlat: editorState.isFlat,
                    symbolKey: editorState.symbolKey,
                    sustain: editorState.sustain,
                }
            });
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            const reffer = this.state.noteData.dataList[focusIndex].reffer as HTMLDivElement;
            reffer.style.backgroundColor = '#ff0';
            setTimeout(() => {
                reffer.style.backgroundColor = '';
            }, 350);
        }
    }

    /**
     * 
     *
     * @memberof Dispatcher
     */
    public backing = {
        toggleMatrix: (channelIndex: number, lenIndex: number) => {
            const backing = this.state.noteState.backingPaletteState;
            const sideIndex = backing.sideIndex;
            const isUse = !this.state.noteState.backingPaletteState.layers[sideIndex].table[channelIndex][lenIndex][0];
            this.dispatchBase({ type: ActionType.BACKING_TOGGLE_MATRIX, payload: { sideIndex, channelIndex, lenIndex, isUse } });
        },
        addLen: () => {
            // this.dispatch({ type: ActionType.ADD_LEN, payload: null });

            const backingPaletteState = this.state.noteState.backingPaletteState;
            const script = backingPaletteState.layers;
            const sideIndex = backingPaletteState.sideIndex;
            const curSideScript = script[sideIndex];
            const selectedLenIndex = backingPaletteState.selectedLenIndex;
            const table = curSideScript.table;
            curSideScript.lens = [...curSideScript.lens];
            // 
            curSideScript.lens.splice(
                selectedLenIndex + 1, 0,
                { len: BACKING_LEN_TYPE.LEN4, isHalf: false, isDiv3: false }
            );
            table.forEach((line) => {
                line.splice(
                    selectedLenIndex + 1, 0, [false]
                );
            });
            curSideScript.vels.splice(
                backingPaletteState.selectedLenIndex + 1, 0, 5
            );
            script[sideIndex] = curSideScript;

            this.dispatchBase({ type: ActionType.INIT_BACKING_PALETTE, payload: { backingPaletteState } });
        },
        delLen: () => {
            // this.dispatch({ type: ActionType.ADD_LEN, payload: null });

            const backingPaletteState = this.state.noteState.backingPaletteState;
            const script = backingPaletteState.layers;
            const sideIndex = backingPaletteState.sideIndex;
            const curSideScript = script[sideIndex];
            const selectedLenIndex = backingPaletteState.selectedLenIndex;
            const table = curSideScript.table;
            curSideScript.lens = [...curSideScript.lens];
            // 
            curSideScript.lens.splice(selectedLenIndex, 1);
            table.forEach((line) => {
                line.splice(selectedLenIndex, 1);
            });
            curSideScript.vels.splice(selectedLenIndex, 1);
            script[sideIndex] = curSideScript;

            if (selectedLenIndex > 0) {
                backingPaletteState.selectedLenIndex--;
            }
            this.dispatch(ActionType.INIT_BACKING_PALETTE, { backingPaletteState });
        },
        updateLenIndex: (index: number) => {
            const backingPaletteState = this.state.noteState.backingPaletteState;
            backingPaletteState.selectedLenIndex = index;
            this.initBackingPalette(backingPaletteState);
        },
        getCurLenIndex: (): number => {
            const backingPaletteState = this.state.noteState.backingPaletteState;
            return backingPaletteState.selectedLenIndex;
        },
        isFirstCurLen: (): boolean => {
            const backing = this.state.noteState.backingPaletteState;
            return backing.selectedLenIndex == 0;
        },
        isLastCurLen: (): boolean => {
            const backing = this.state.noteState.backingPaletteState;
            const sideScript = backing.layers[backing.sideIndex];
            return backing.selectedLenIndex == sideScript.lens.length - 1;
        },
        getSideScript: (): BackingLayer => {
            const backing = this.state.noteState.backingPaletteState;
            return backing.layers[backing.sideIndex];
        },
        getCurLenBean: (): BackingLen => {
            const backing = this.state.noteState.backingPaletteState;
            const sideScript = backing.layers[backing.sideIndex];
            return sideScript.lens[backing.selectedLenIndex];
        },

        /**
         * 
         * @param lenBean 
         */
        setCurrentLenBean: (lenBean: BackingLen) => {
            const backing = this.state.noteState.backingPaletteState;
            const script = backing.layers;
            const sideScript = script[backing.sideIndex];
            sideScript.lens[backing.selectedLenIndex] = lenBean;
            this.initBackingPalette({ ...backing, layers: script });
        },

        /**
         * 
         * @param lenType 
         */
        setCurLen: (lenType: BACKING_LEN_TYPE) => {
            const curLenBean = this.backing.getCurLenBean();
            curLenBean.len = lenType;
            curLenBean.isHalf = false;
            curLenBean.isDiv3 = false;
            this.backing.setCurrentLenBean(curLenBean);
        },

        /**
         * 
         * @param velocity 110
         */
        updateCurVolume: (velocity: number) => {
            const backing = this.state.noteState.backingPaletteState;
            const script = backing.layers;
            script[backing.sideIndex].vels[backing.selectedLenIndex] = velocity;
            this.initBackingPalette({ ...backing, layers: script });
        },

        /**
         * 
         * @returns 
         */
        getCurVelocity: (): number => {
            const backing = this.state.noteState.backingPaletteState;
            const sideScript = backing.layers[backing.sideIndex];
            return sideScript.vels[backing.selectedLenIndex];
        },

        /**
         * 
         * @returns 
         */
        isAcceptHalf: (): boolean => {
            return [
                BACKING_LEN_TYPE.LEN2,
                BACKING_LEN_TYPE.LEN4,
                BACKING_LEN_TYPE.LEN8
            ].includes(this.backing.getCurLenBean().len);
        },

        /**
         * 3
         * @returns 3
         */
        isAcceptDiv3: (): boolean => {
            return [
                BACKING_LEN_TYPE.LEN4,
                BACKING_LEN_TYPE.LEN8
            ].includes(this.backing.getCurLenBean().len);
        },

        setLayerIndex: (index: number) => {
            const backing = this.state.noteState.backingPaletteState;
            backing.sideIndex = index;
            backing.selectedLenIndex = 0;
            this.initBackingPalette(backing);
        },

        changeLayerIndex: () => {
            const backing = this.state.noteState.backingPaletteState;
            backing.sideIndex = backing.sideIndex == 0 ? 1 : 0;
            backing.selectedLenIndex = 0;
            this.initBackingPalette(backing);
        },

        setUseChannels: (useChannels: ChannelParam) => {
            const backingPaletteState = this.state.noteState.backingPaletteState;
            const baseLayers = backingPaletteState.layers;
            const layers: BackingLayer[] = new Array(2);
            for (let i = 0; i < 2; i++) {
                const channelCount = backingPaletteState.useChannels.length;
                const lensSplitCount = baseLayers[i].lens.length;
                const baseTable = baseLayers[i].table;
                const table: boolean[][][] = new Array(channelCount);

                for (let y = 0; y < channelCount; y++) {
                    if (y < baseTable.length) {
                        table[y] = baseTable[y];
                    } else {
                        table[y] = new Array(lensSplitCount);
                        for (let x = 0; x < lensSplitCount; x++) {
                            table[y][x] = new Array(1);
                            table[y][x][0] = false;
                        }
                    }
                }
                layers[i] = {
                    ...baseLayers[i],
                    table,
                };
            }
        },

        setCurBackingScript: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const chordDetail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            const editorState = this.state.noteState.backingPaletteState;
            const backing = {} as BackingScript;
            backing.useChannels = editorState.useChannels;
            backing.layers = editorState.layers;
            this.dispatch(ActionType.DETAIL_PARAMS_SETTER, {
                pos: focusIndex,
                params: {
                    ...chordDetail,
                    backing
                }
            });
            this.dispatch(ActionType.FLOATWND_STATE, { state: FloatWndState.NONE });
            const reffer = this.state.noteData.dataList[focusIndex].reffer as HTMLDivElement;
            reffer.style.backgroundColor = '#ff0';
            setTimeout(() => {
                reffer.style.backgroundColor = '';
            }, 350);
        },

        getNotesCount: (backing: BackingScript): number => {

            let count = 0;
            backing.layers.forEach((layer) => {
                layer.table.forEach((line) => {
                    line.forEach((cell) => {
                        if (cell[0]) {
                            count++;
                        }
                    })
                })
            })
            return count;
        },

        play: () => {
            const focusIndex = this.state.noteState.focusIndex;
            const detail = this.state.noteData.dataList[focusIndex].detail as DetailChord;
            const symbol = getSymbolParams(detail.symbolKey);
            const intervals = symbol?.intervals as number[];
            const backing = this.state.noteState.backingPaletteState;
            const tempo = 130;
            // 1
            const barMs = 60000 / tempo * 4;
            // 
            setTimeout(() => {
                this.state.sampler.releaseAll();
            }, barMs);
            for (let i = 0; i < 2; i++) {
                let headMs = 1;
                const sideScript = backing.layers[i];
                // 
                sideScript.lens.forEach((lenBean, j) => {

                    const lenValue = Number(lenBean.len.toString().replace('LEN', ''));
                    let curLenMs = barMs / lenValue;
                    if (lenBean.isHalf) {
                        curLenMs *= 1.5;
                    }
                    backing.useChannels.forEach((channel, k) => {

                        if (sideScript.table[k][j][0]) {
                            const soundIndex = detail.degreeIndex + intervals[channel.structIndex] + channel.octave * 12;
                            const octave = Math.floor(soundIndex / 12);
                            const root = KEY12_SHARP_LIST[soundIndex % 12];
                            const soundFullName = root + octave;
                            const velocity = 0.7 * sideScript.vels[j] / 10;
                            // alert(soundFullName);
                            setTimeout(() => {
                                this.state.sampler.triggerAttack(soundFullName, '+0', velocity);
                            }, headMs);
                        }
                    });
                    headMs += curLenMs;
                });
            }
        }
    }
}