import { BackingLayer, BACKING_LEN_TYPE } from "contents/redux/store";


type SplitMeasureProps = {
    layer: BackingLayer;
}

const SplitMeasure = (props: SplitMeasureProps) => {

    const curSideScript = props.layer;

    // 
    const measureInner: JSX.Element[] = [];
    let measureLength = 1;
    curSideScript.lens.forEach((lenBean, i) => {
        let cnt = 0;
        switch (lenBean.len) {
            case BACKING_LEN_TYPE.LEN1: cnt = 16; break;
            case BACKING_LEN_TYPE.LEN2: cnt = 8; break;
            case BACKING_LEN_TYPE.LEN4: cnt = 4; break;
            case BACKING_LEN_TYPE.LEN8: cnt = 2; break;
            case BACKING_LEN_TYPE.LEN16: cnt = 1; break;
        }
        if (lenBean.isHalf) {
            cnt *= 1.5;
        }
        measureLength += cnt;
    });
    for (let i = 0; i < measureLength; i++) {
        const getLen = () => {
            if (i % 2 == 0) {
                if (i % 4 == 0) {
                    return 'mem4';
                } else {
                    return 'mem8';
                }
            } else {
                return 'mem16';
            }
        }
        const isLast = i == measureLength - 1;
        measureInner.push(<div key={i} className={getLen() + (isLast ? ' last' : '')} />);
    }

    return (
        <div className="measure">
            {measureInner}
        </div>
    );
}

export default SplitMeasure;